@import url(https://fonts.googleapis.com/css?family=Montserrat|Staatliches&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.form-item-checkbox {
    position: relative !important;
    opacity: 1 !important;
    pointer-events: none;
}
.row {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 15px;
}

.bags {
    display: flex;
    align-items: flex-end;
    height: 16rem;
    background-color: #2E4158;
    box-shadow: 10px 10px rgba(51, 51, 51, 0.418);
    color: white;
    /* border-radius: 90px 90px 90px 90px; */
    padding: 15px;
}

.bags:hover {
    background: -webkit-gradient(linear, left top, left bottom, from(#e66465), to(#9198e5));
    background: linear-gradient(#e66465, #9198e5);
}

.bags-name {
    font-size: 130%;
    margin: 5px;
    text-shadow: 5px 5px 5px rgba(20, 20, 20, 0.418);
}

.home-name {
    font-size: 250%;
    margin: 5px;
    font-family: 'Staatliches', cursive;
    padding: 20px;
    text-shadow: 5px 5px 5px rgba(20, 20, 20, 0.418);
}

.bags-content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about {
	background: -webkit-gradient(linear, left top, left bottom, from(#2E4158), to(#111111));
	background: linear-gradient(#2E4158, #111111);
    color: white;
}

.about, .col.l3 {
    margin: 20px !important;
}

.row .col {
    padding: 0;
}
.spinner-wrapper {
    display: flex;
    justify-content: center;
}
.container {
    width: 100%;
}

.review-content {
    display: flex;
    flex-direction: column;
    color: white;
    width: 100%;
}

h1 {
    font-size: 7rem;
    font-family: 'Staatliches', cursive;
    text-shadow: 10px 10px rgba(51, 51, 51, 0.418);
}

h2 {
    font-size: 1.7rem;
}

h3 {
    font-size: 2rem;
    font-family: 'Staatliches', cursive;
}

@media only screen and (min-width: 360px){
    h1 {
        font-size: 5rem;
        font-family: 'Staatliches', cursive;
        text-shadow: 10px 10px rgba(51, 51, 51, 0.418);
    }
}
.hamburger-container {
    z-index: 99px;
}

.hamburger {
    background-color: rgb(208, 130, 94) !important;
    border: none;
}

.hamburger-text {
    color: white !important;
    font-size: 2rem;
}

.menu {
    position: absolute;
    background: -webkit-gradient(linear, left top, left bottom, from(rgb(208, 130, 94)), to(rgb(179, 93, 54)));
    background: linear-gradient(rgb(208, 130, 94), rgb(179, 93, 54));
    z-index: 0;
    width: 78%;
    height: 100vh;
    overflow: auto;
    padding: 10px;
    border-radius: 35px 35px;
}

@media only screen and (min-width: 414px){
    .hamburger-container, .hamburger {
        display: none;
    }
}
:root {
	background-color: black;
}

.app {
	height: 100vh;
	display: flex;
	color: white;
	font-family: 'Montserrat', sans-serif;
}
  
.app-left {
	width: 50%;
	/* background-color: rgb(208, 130, 94); */
	background: -webkit-gradient(linear, left top, left bottom, from(rgb(208, 130, 94)), to(rgb(179, 93, 54)));
	background: linear-gradient(rgb(208, 130, 94), rgb(179, 93, 54));
	overflow: auto;
    box-shadow: 10px 10px rgba(51, 51, 51, 0.418);
}
  
.app-right {
	width: 50%;
	/* background-color: #2E4158; */
	background: -webkit-gradient(linear, left top, left bottom, from(#2E4158), to(#111111));
	background: linear-gradient(#2E4158, #111111);
	padding: 25px 40px;
	overflow: auto;
}

@media only screen and (max-width: 414px){
	.app {
		height: 100vh;
		display: flex;
		color: white;
		font-family: 'Montserrat', sans-serif;
	}
	.app-left {
		width: 0%;
		/* background-color: rgb(208, 130, 94); */
		background: -webkit-gradient(linear, left top, left bottom, from(rgb(208, 130, 94)), to(rgb(179, 93, 54)));
		background: linear-gradient(rgb(208, 130, 94), rgb(179, 93, 54));
		overflow: auto;
		box-shadow: 10px 10px rgba(51, 51, 51, 0.418);
	}
	  
	.app-right {
		width: 100%;
		/* background-color: #2E4158; */
		background: -webkit-gradient(linear, left top, left bottom, from(#2E4158), to(#111111));
		background: linear-gradient(#2E4158, #111111);
		padding: 25px 40px;
		overflow: auto;
	}
}
  
.toggle {
	display: flex;
	justify-content: flex-end;
	margin-bottom: 10%;
}
  
.toggle-item {
	background-color: #4C5D72;
	color: #9DA6B1;
	padding: 10px 25px;
	cursor: pointer;
	font-size: .9em;
	border: none;
	outline: none;
	display: inline-block;
	text-decoration: none;
    box-shadow: 10px 10px rgba(51, 51, 51, 0.418);
}
  
.toggle-item-active {
	background-color: rgb(208, 130, 94);
	color: white;
}
  
.toggle-item:first-child {
	border-top-left-radius: 25px;
	border-bottom-left-radius: 25px;
}

.toggle-item:last-child {
	border-top-right-radius: 25px;
	/* border-bottom-right-radius: 25px; */
}

.hamburger,
.collapsed {
	display: flex;
	justify-content: center;
	align-items: center;
    position: absolute;
	bottom: 1em;
	right: 1em;
	border-radius: 50px;
	height: 50px;
	width: 50px;
	background-color: pink;
	box-shadow: #111111;
}

.hamburger-text {
	color: black;
	text-shadow: 2px 2px #8a8a8a;
}

.form-main-container {
	margin-bottom: 100px;
}
  
.form-item {
	margin-bottom: 40px;
}
  
.form-item-label {
	display: block;
	text-transform: uppercase;
	font-size: .9em;
	color: white;
}

.form-item-input {
	width: 85%;
	background-color: transparent;
	border: none;
	color: white;
	outline: none;
	border-bottom: 1px solid #445366;
	font-size: 1em;
	font-weight: 300;
	padding-bottom: 10px;
	margin-top: 10px;
}
  
.form-item-input::-webkit-input-placeholder {
	color: #616E7F;
}
  
.form-item-input::-moz-placeholder {
	color: #616E7F;
}
  
.form-item-input:-ms-input-placeholder {
	color: #616E7F;
}
  
.form-item-input::-ms-input-placeholder {
	color: #616E7F;
}
  
.form-item-input::placeholder {
	color: #616E7F;
}
  
.form-item-button {
	background-color: rgb(208, 130, 94);
	color: white;
	border: none;
	outline: none;
	border-radius: 25px 25px 0 25px;
	padding: 15px 70px;
	font-size: .8em;
	font-weight: 500;
	box-shadow: 10px 10px rgba(51, 51, 51, 0.418);
	font-family: 'Montserrat', sans-serif;
}
 
.form-item-checkbox-label {
	color: #646F7D;
	font-size: .9em;
}
  
.form-item-checkbox {
	position: relative;
	top: 1.5px;
}
  
.form-item-tos {
	color: white;
	border-bottom: 1px solid rgb(208, 130, 94);
	text-decoration: none;
	display: inline-block;
	padding-bottom: 2px;
	margin-left: 5px;
}
